import React, { useState } from 'react'
import styled from 'styled-components/macro';
import Flip from 'react-reveal/Flip';
import Fade from 'react-reveal/Fade';
import Fab from '@material-ui/core/Fab';
import IconButton from '@material-ui/core/IconButton';
import VisibilityIcon from '@material-ui/icons/Visibility';
import SwapVertIcon from '@material-ui/icons/SwapVert';
import BackIcon from '@material-ui/icons/NavigateBefore';
import NextIcon from '@material-ui/icons/NavigateNext';

// Create a Title component that'll render an <h1> tag with some styles
const Title = styled.h1`
    text-align: center;
    font-size: 2.5em;
    font-family: Rubik;
    color: white;
    margin-top:1.5em;
    margin-bottom:1.5em;
`;

// Create a Wrapper component that'll render a <section> tag with some styles
const Wrapper = styled.section`
  background-color: indianred;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  /* justify-content: space-evenly; */
  align-items:center;
  height:calc(100vh - 5vh - 0.5em);
  width:100vw;
  padding-top: 5vh;
  padding-bottom: 0.5em;
`;

const Navigation = styled.section`
  display: flex;
  justify-content: space-around;
  align-items:center;
  margin-top:auto;
  width:100%;
`;

const Button = styled.button`
  /* Adapt the colors based on primary prop */
  background: ${props => props.primary ? "palevioletred" : "white"};
  color: ${props => props.primary ? "white" : "palevioletred"};
  font-size: 1em;
  font-family: Rubik;
  /* font-weight: bold; */
  text-transform: uppercase;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 5px;
`;

export default ({ question, answer, ...props }) => {

    let [showAnswer, setShowAnswer] = useState(false);

    function handleClick(e) {
        e.stopPropagation();
        setShowAnswer(true);
    }

    return <Wrapper>
        <Fade right big cascade>
            <Title>{question}</Title>
        </Fade>
        <IconButton onClick={props.onReverse} style={{color:'white', opacity:0.3}}><SwapVertIcon fontSize='large'/></IconButton>
        {showAnswer &&
            <Flip top slide>
                <Title>{answer}</Title>
            </Flip>
        }
        <Navigation>
            <IconButton onClick={props.onBack} style={{color: 'white', opacity:0.7 }} ><BackIcon fontSize='large'/></IconButton>
            <IconButton disabled={showAnswer} onClick={handleClick} style={{color: 'white', opacity:0.7 }} ><VisibilityIcon fontSize='large'/></IconButton>
            <IconButton onClick={props.onNext} style={{color: 'white', opacity:0.7 }} ><NextIcon fontSize='large'/></IconButton>
        </Navigation>
    </Wrapper>

}