import React, { useState } from 'react';
import Card from './Card';
import espa√±ol from './todos-verbos';
import english from './all-verbs';
import './App.css';
// const espa√±ol = ['cantar', 'hablar', 'haber', 'vivir', 'adivinar', 'correr', 'andar'];
// const english = ['to sing', 'to speak', 'to have', 'to live', 'to guess', 'to run', 'to walk'];

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

const history = [];

function App() {

  let [next, setNext] = useState(58);
  let [reverse, toggleReverse] = useState(false);

  let questions = espa√±ol;
  let answers = english;

  function handleNext() {
    const _next = getRandomInt(questions.length - 1);
    history.push(_next);
    setNext(_next);
  }

  function handleBack() {
    history.pop();
    setNext(history[history.length - 1] || 0);
  }

  if (reverse) {
    questions = english;
    answers = espa√±ol;
  }

  return <div className="App">
    <Card
      key={questions[next]}
      question={questions[next]}
      answer={answers[next]}
      onNext={handleNext}
      onBack={handleBack}
      onReverse={() => toggleReverse(!reverse)}
    />
  </div>
}

export default App;
